@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
.stars-gray{position:relative;display:inline-block}.stars-yellow{position:absolute;top:0;left:0;white-space:nowrap;overflow:hidden;width:0}.stars-gray::before{content:"\f005 \f005 \f005 \f005 \f005";font-family:"Font Awesome 5 Free";font-weight:900;color:#ccc}.stars-yellow::before{content:"\f005 \f005 \f005 \f005 \f005";font-family:"Font Awesome 5 Free";font-weight:900;color:#f8ce0b}

.button-detail{height:100%;cursor:pointer;background-color:transparent;border:none;display:flex;flex-direction:column;justify-content:space-between;align-items:center;color:#fff}.button-detail .image{border-radius:0.2rem;width:100%}

.taggle-button{color:#3d405b;border-radius:1.5rem;box-shadow:rgba(60,64,67,0.3) 0px 1px 2px 0px,rgba(60,64,67,0.15) 0px 1px 3px 1px;height:50px;border:none;display:flex;justify-content:center;align-items:center;margin-top:0.5rem;cursor:pointer;width:85%;height:3rem}.taggle-button .taggle-button-icon{margin-right:1rem}.taggle-button:hover{background-color:#fcd582}.taggle-button:active{box-shadow:none;background-color:#e07a5f;border-color:#e07a5f}.taggle-button:focus{box-shadow:rgba(60,64,67,0.3) 0px 1px 2px 0px,rgba(60,64,67,0.15) 0px 1px 3px 1px}

.details{display:flex;justify-content:center;padding:1rem;border-radius:1rem}.details .details-videos{display:flex;flex-direction:column;align-items:center;margin-right:1rem}.details .details-videos .text-title{font-size:3rem}.details .details-videos .text-overview{margin-top:3rem;margin-bottom:1rem}.details .details-videos .container-text{display:flex;flex-direction:column;width:100%}.details .details-videos .container-text .trailers{margin:1rem}.details .details-videos .container-videos{display:flex;flex-wrap:wrap;flex-direction:row;justify-content:center}.details .details-videos .container-videos .video{margin:0.5rem}.details .image{width:100%;height:auto}

.button-detail{height:100%;cursor:pointer;background-color:transparent;border:none;display:flex;flex-direction:column;justify-content:space-between;align-items:center;color:#fff}.button-detail .image{border-radius:0.2rem;width:100%}

.main{background-color:#10002b;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}

.footer{width:100%;min-height:5rem;display:flex;justify-content:center;align-items:center;background-color:#ff0054;margin-top:2rem}.footer p{margin:0.5rem}

.pagination{padding:0.5rem;margin-top:1rem;display:flex;justify-content:center;background-color:#ff0054;border-radius:2rem}

.navBar{padding:0.5rem;font-size:1rem;border-bottom:1px solid #ff0054}.navBar .name-app{color:#ff0054;font-family:'Quicksand', sans-serif;font-weight:600;font-size:2rem;margin-left:1rem;letter-spacing:1rem}.navBar .link-nav{text-decoration:none;margin:1rem;color:#fff}.navBar .nav-button{margin:1rem;color:#fff;border:none;font-size:1rem;background-color:transparent}.nav-responsive{border-bottom:1px solid #ff0054;display:flex;align-items:center}.nav-responsive .button-menu{border-radius:1rem;background-color:transparent;border:none;width:2rem;height:2rem;margin-left:0.5rem}.nav-responsive .button-menu .menu{border:none;color:#ff0054;font-size:2rem;cursor:pointer}.nav-responsive .button-menu .menu:hover{color:#e59500}.nav-responsive-options{background-color:#10002b;flex-direction:column;border-bottom:1px solid #ff0054;padding:1rem}.nav-responsive-options .link-nav{text-decoration:none;background-color:#10002b;color:#fff}.nav-responsive-options .link-nav:hover{color:#ff0054}.nav-responsive-options .nav-button{background-color:#10002b;border:none;color:#fff;text-align:left;font-size:1rem;font-family:'Quicksand', sans-serif}.nav-responsive-options .nav-button:hover{color:#ff0054}.nav-responsive-options .close-button{border:none;background-color:transparent;color:#ff0054;cursor:pointer;display:flex;justify-content:flex-end}.nav-responsive-options .close-button:hover{color:#e59500}

.header{width:100%}

.home{width:80%;min-height:80vh}.home .hello-text{font-size:2.5rem;font-weight:600;margin-bottom:2rem;color:#fff}

.login-form{width:100%;padding:1rem;background-color:#ffffff10;backdrop-filter:blur(12px);-webkit-backdrop-filter:blur(12px);border-radius:1rem;color:white;display:flex;flex-direction:column}.login-form .title-login-form{margin-bottom:1rem;text-align:center;font-size:2rem}.login-form .container-input{display:flex;flex-direction:column;margin-bottom:1rem}.login-form .container-input .input{height:2rem;padding:1rem;border:none;border-radius:0.3rem;margin-top:0.5rem}.login-form .button-container{display:flex;justify-content:center;margin-top:2rem}.login-form .button-container .button{width:50%;height:3rem;border-radius:2rem;background-color:#ff0054;border:none;justify-self:center;color:#fff;font-size:1rem}.login-form .button-container .button:hover{background-color:#e59500}.login-form .link-container{display:flex;justify-content:flex-end}.login-form .link-container .link-login-form{text-decoration:none;color:#fff}

.login{width:100%;height:100vh;display:flex;justify-content:center;align-items:center;padding:2rem;position:absolute;z-index:99999}.login .cinema-image{width:100%}.carousel{width:100vw;position:absolute;top:0;left:0;z-index:0}.box-carousel-image{object-fit:contain;background-size:cover;background-repeat:no-repeat;width:100vw;height:100vh;background-position:center}

.singup-form{width:40rem;padding:1rem;background-color:#ffffff10;backdrop-filter:blur(12px);-webkit-backdrop-filter:blur(12px);border-radius:1rem;color:white}.singup-form .form{margin:1rem;display:flex;flex-direction:column}.singup-form .form .title-sign-up{margin-bottom:1rem;text-align:center;font-size:2rem}.singup-form .form .container-input{display:flex;flex-direction:column;margin-bottom:1rem}.singup-form .form .container-input .input{height:2rem;padding:1rem;border:none;border-radius:0.3rem;margin-top:0.5rem}.singup-form .form .container-button{width:100%;display:flex;justify-content:center}.singup-form .form .container-button .button{width:50%;height:3rem;border-radius:2rem;background-color:#ff0054;border:none;justify-self:center;color:#fff;font-size:1rem}.singup-form .form .container-button .button:hover{background-color:#e59500}

.singup{min-height:90vh;width:99vw;display:flex;justify-content:center;align-items:center;background-image:url(/tp-final-cinema/static/media/it.c0e354f1.jpeg);background-size:cover}

.users-table{min-height:75vh}

.admin{min-height:80vh;min-width:80%}.admin .text-no-items{color:#fff;font-size:3rem}

.movies{width:80%;min-height:80vh}.movies .text-no-items{color:#fff;font-size:3rem}

.series{width:80%;min-height:75vh}.series .text-no-items{color:#fff;font-size:3rem}

.details-page{width:100%;min-height:80vh;display:flex;flex-direction:column;align-items:center}.details-page .go-back{display:flex;justify-content:center;align-items:center;margin:1rem;padding:1rem;border-radius:2rem;border:none;width:10rem;background-color:#041738;color:#fff;cursor:pointer}.details-page .go-back:hover{background-color:#ff0054}

